import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Tabs,
  Tab,
  Button,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  IconButton,
  CircularProgress
} from '@mui/material';
import { Download as DownloadIcon } from '@mui/icons-material';
import { getFacultyReports, downloadReport } from '../services/api';

function FacultyDashboard() {
  const [tabValue, setTabValue] = useState(0);
  const [openDialog, setOpenDialog] = useState(false);
  const [reports, setReports] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [selectedReport, setSelectedReport] = useState(null);

  useEffect(() => {
    fetchReports();
  }, []);

  const fetchReports = async () => {
    try {
      setLoading(true);
      const data = await getFacultyReports();
      setReports(data);
      setError(null);
    } catch (err) {
      setError('Failed to load reports');
      console.error('Error fetching reports:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  const handleOpenDialog = (report) => {
    setSelectedReport(report);
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setSelectedReport(null);
    setOpenDialog(false);
  };

  const handleDownloadReport = async (reportId) => {
    try {
      const data = await downloadReport(reportId);
      const url = window.URL.createObjectURL(new Blob([data]));
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', `report-${reportId}.pdf`);
      document.body.appendChild(link);
      link.click();
      link.remove();
    } catch (err) {
      console.error('Error downloading report:', err);
      alert('Failed to download report');
    }
  };

  const renderReportList = () => {
    if (loading) {
      return (
        <Box sx={{ display: 'flex', justifyContent: 'center', p: 3 }}>
          <CircularProgress />
        </Box>
      );
    }

    if (error) {
      return (
        <Typography color="error" align="center">
          {error}
        </Typography>
      );
    }

    if (reports.length === 0) {
      return (
        <Typography align="center" color="textSecondary">
          No reports available
        </Typography>
      );
    }

    return (
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Student</TableCell>
              <TableCell>Title</TableCell>
              <TableCell>Semester</TableCell>
              <TableCell>Submission Date</TableCell>
              <TableCell>Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {reports.map((report) => (
              <TableRow key={report._id}>
                <TableCell>
                  {report.student.firstName} {report.student.lastName}
                  <Typography variant="caption" display="block" color="textSecondary">
                    Roll: {report.student.rollNumber}
                  </Typography>
                </TableCell>
                <TableCell>{report.title}</TableCell>
                <TableCell>{report.semester}</TableCell>
                <TableCell>
                  {new Date(report.submissionDate).toLocaleDateString()}
                </TableCell>
                <TableCell>
                  <IconButton
                    color="primary"
                    onClick={() => handleOpenDialog(report)}
                    title="View Details"
                  >
                    <Typography variant="button">View</Typography>
                  </IconButton>
                  {report.reportFile && (
                    <IconButton
                      color="primary"
                      onClick={() => handleDownloadReport(report._id)}
                      title="Download Report"
                    >
                      <DownloadIcon />
                    </IconButton>
                  )}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    );
  };

  return (
    <Container maxWidth="lg">
      <Box sx={{ mt: 4 }}>
        <Typography variant="h4" gutterBottom>
          Faculty Dashboard
        </Typography>
        <Typography variant="subtitle1" color="textSecondary" gutterBottom>
          Welcome, {localStorage.getItem('username')}!
        </Typography>

        <Box sx={{ mt: 3 }}>
          <Tabs value={tabValue} onChange={handleTabChange}>
            <Tab label="Student Reports" />
            <Tab label="My Courses" />
            <Tab label="Assignments" />
          </Tabs>

          <Box sx={{ mt: 3 }}>
            {tabValue === 0 && (
              <Grid container spacing={3}>
                <Grid item xs={12}>
                  <Card>
                    <CardContent>
                      <Typography variant="h6" gutterBottom>
                        Student Reports
                      </Typography>
                      {renderReportList()}
                    </CardContent>
                  </Card>
                </Grid>
              </Grid>
            )}

            {tabValue === 1 && (
              <Grid container spacing={3}>
                <Grid item xs={12}>
                  <Card>
                    <CardContent>
                      <Typography variant="h6" gutterBottom>
                        Current Courses
                      </Typography>
                      {/* Course list will be added here */}
                    </CardContent>
                  </Card>
                </Grid>
              </Grid>
            )}

            {tabValue === 2 && (
              <Grid container spacing={3}>
                <Grid item xs={12}>
                  <Card>
                    <CardContent>
                      <Typography variant="h6" gutterBottom>
                        Current Assignments
                      </Typography>
                      {/* Assignment list will be added here */}
                    </CardContent>
                  </Card>
                </Grid>
              </Grid>
            )}
          </Box>
        </Box>
      </Box>

      <Dialog open={openDialog} onClose={handleCloseDialog} maxWidth="md" fullWidth>
        <DialogTitle>Report Details</DialogTitle>
        {selectedReport && (
          <DialogContent>
            <Grid container spacing={2}>
              <Grid item xs={12} sm={6}>
                <Typography variant="subtitle2">Student Name</Typography>
                <Typography gutterBottom>
                  {selectedReport.student.firstName} {selectedReport.student.lastName}
                </Typography>
              </Grid>
              <Grid item xs={12} sm={6}>
                <Typography variant="subtitle2">Roll Number</Typography>
                <Typography gutterBottom>{selectedReport.student.rollNumber}</Typography>
              </Grid>
              <Grid item xs={12} sm={6}>
                <Typography variant="subtitle2">Semester</Typography>
                <Typography gutterBottom>{selectedReport.semester}</Typography>
              </Grid>
              <Grid item xs={12} sm={6}>
                <Typography variant="subtitle2">Academic Year</Typography>
                <Typography gutterBottom>{selectedReport.academicYear}</Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography variant="subtitle2">Title</Typography>
                <Typography gutterBottom>{selectedReport.title}</Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography variant="subtitle2">Description</Typography>
                <Typography gutterBottom>{selectedReport.description}</Typography>
              </Grid>
              <Grid item xs={12}>
                <Typography variant="subtitle2">Submission Date</Typography>
                <Typography gutterBottom>
                  {new Date(selectedReport.submissionDate).toLocaleString()}
                </Typography>
              </Grid>
            </Grid>
          </DialogContent>
        )}
        <DialogActions>
          {selectedReport?.reportFile && (
            <Button
              onClick={() => handleDownloadReport(selectedReport._id)}
              color="primary"
              startIcon={<DownloadIcon />}
            >
              Download Report
            </Button>
          )}
          <Button onClick={handleCloseDialog} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
}

export default FacultyDashboard;
